"use client"; // ✅ Converts the component to a Client Component

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import getProduct from "@/actions/get-product";
import getProducts from "@/actions/get-products";
import Gallery from "@/components/gallery";
import ProductList from "@/components/product-list";
import Container from "@/components/ui/container";

const ProductPage = () => {
    const { productId } = useParams(); // ✅ Uses `useParams()` to get the ID dynamically
    const [product, setProduct] = useState(null);
    const [suggestedProducts, setSuggestedProducts] = useState([]);

    useEffect(() => {
        if (!productId) return;

        const fetchProduct = async () => {
            const fetchedProduct = await getProduct(productId);
            setProduct(fetchedProduct);

            if (fetchedProduct) {
                const related = await getProducts({ categoryId: fetchedProduct?.category?.id });
                setSuggestedProducts(related);
            }
        };

        fetchProduct();
    }, [productId]);

    if (!product) {
        return <div>Product not found</div>;
    }

    return (
        <div className="bg-white">
            <Container>
                <div className="px-4 py-10 sm:px-6 lg:px-8">
                    <div className="lg:grid lg:grid-cols-2 lg:items-start lg:gap-x-8">
                        <Gallery images={product.images} />
                        <div className="mt-10 px-4 sm:mt-16 sm:px-0 lg:mt-0">
                            Info
                        </div>
                    </div>
                    <hr className="my-10" />
                    <ProductList title="Related Items" items={suggestedProducts} />
                </div>
            </Container>
        </div>
    );
};

export default ProductPage;
